import Vue       from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import app                          from '../App.vue'                                           // 
import bottomNav                    from '../views/BottomNav.vue'                               // 
import discover                     from '../views/Discover.vue'                                // "" 
import houseResource                from '../views/HouseResource.vue'                           // "" 
import map                          from '../views/Map.vue'                                     // "" 
import service                      from '../views/Service.vue'                                 // "" 

// 
import service_router               from '../components/Service/Service_router.vue'             // ""  - 
import service_furnichure_router    from '../components/Service/Service_furnichure_router.vue'  // ""  - ''

const router = new VueRouter({
    routes: [
        {
            path: 'app',
            components: {
                default: app
            }
        },
        {
            path: '/',
            name:'index',
            components: { default: app },
            children: [
                {
                    path: '/',
                    components: {
                        AppContent      : discover,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/discover',
                    components: {
                        AppContent      : discover,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/houseResource',
                    components: {
                        AppContent      : houseResource,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/map',
                    components: {
                        AppContent      : map,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/service',
                    components: {
                        AppContent      : service,
                        AppBottomNav    : bottomNav
                    }
                },
                //  ,  2
                // {
                //     path: '/service/:id',
                //     components: {
                //         AppContent      : service_router
                //     },
                //     children: [
                //         {
                //             //  /user/:id/profile 
                //             // UserProfile  User  <router-view> 
                //             // path: '', component: UserHome
                //             path: 'furnichure/:id',
                //             components: {
                //                 AppContent      : service_furnichure_router
                //             }
                //         }
                //     ]
                // }
                {
                    path: '/service/:id',
                    components: {
                        AppContent      : service_router
                    }
                },
                //  - '' 
                {
                    path: '/service/furnichure/:id',
                    components: {
                        AppContent      : service_furnichure_router
                    }
                }
            ]
        }
    ]
})

export default {
    router
}



